import type { DefaultTreeAdapterMap } from 'parse5';

export function traverseModuleScripts(
  node: DefaultTreeAdapterMap['node'],
  visitor: (node: DefaultTreeAdapterMap['element']) => void
): void {
  traverseNodes(node, (child) => {
    if (
      nodeIsElement(child) &&
      child.nodeName === 'script' &&
      // only modules
      child.attrs.some(
        (attr) => attr.name === 'type' && attr.value === 'module'
      ) &&
      // no inline scripts
      child.attrs.some((attr) => attr.name === 'src')
    ) {
      visitor(child);
    }

    if (
      nodeIsElement(child) &&
      child.nodeName === 'link' &&
      // only modules
      child.attrs.some(
        (attr) => attr.name === 'rel' && attr.value === 'modulepreload'
      )
    ) {
      visitor(child);
    }
  });
}

function nodeIsElement(
  node: DefaultTreeAdapterMap['node']
): node is DefaultTreeAdapterMap['element'] {
  return node.nodeName[0] !== '#';
}

function traverseNodes(
  node: DefaultTreeAdapterMap['node'],
  visitor: (node: DefaultTreeAdapterMap['node']) => void
): void {
  if (node.nodeName === 'template') {
    node = (node as DefaultTreeAdapterMap['template']).content;
  }

  visitor(node);

  if (
    nodeIsElement(node) ||
    node.nodeName === '#document' ||
    node.nodeName === '#document-fragment'
  ) {
    for (const childNode of node.childNodes) {
      traverseNodes(childNode, visitor);
    }
  }
}
