import type { BaseDevCenterComponent } from '@wix/dev-center-schemas';

export interface GenericExtension {
  type: 'GenericExtension';
  options: BaseDevCenterComponent;
}

export function genericExtension(
  options: BaseDevCenterComponent
): GenericExtension {
  return {
    type: 'GenericExtension',
    options,
  };
}
