import { ok } from 'node:assert';
import { join } from 'node:path';
import type { build, Rollup, ViteDevServer } from 'vite';

type ViteBuildOutput = Awaited<ReturnType<typeof build>>;

export function appendToDevServerUrl(
  devServerUrl: string,
  relativePath: string
) {
  const url = new URL(
    relativePath.replace(/^[\\/]/, './'),
    devServerUrl.endsWith('/') ? devServerUrl : `${devServerUrl}/`
  );

  return url.toString();
}

export function getAssetUrlForModuleId({
  baseUrl,
  devServer,
  moduleId,
}: {
  baseUrl: string;
  devServer: ViteDevServer;
  moduleId: string;
}) {
  const pathWithBase = join(devServer.config.base, '@id', `${moduleId}?import`);

  return appendToDevServerUrl(baseUrl, pathWithBase);
}

export function getLocalDevServerUrl(server: ViteDevServer) {
  const [devServerUrl] = server.resolvedUrls?.local ?? [];
  ok(devServerUrl != null);

  return devServerUrl;
}

export function viteBuildOutputToRollupOutputs(
  viteBuildOutput: ViteBuildOutput
) {
  const result: Rollup.RollupOutput[] = [];
  if (Array.isArray(viteBuildOutput)) {
    result.push(...viteBuildOutput);
  } else if ('output' in viteBuildOutput) {
    result.push(viteBuildOutput);
  }
  return result;
}
