import { writeFile } from 'node:fs/promises';
import { join } from 'node:path';
import { normalizePath, outputDir } from '@wix/fs-utils';
import { outdent } from 'outdent';

const wrapComponentUrl = new URL(
  '../dependencies/astro-backoffice-extensions/browser-runtime/wrapComponent.js',
  import.meta.url
);

export async function buildBackofficeComponent({
  compId,
  codegenDir,
  entryFileName,
  srcDir,
}: {
  compId: string;
  codegenDir: string;
  entryFileName: string;
  srcDir: string;
}): Promise<void> {
  const directory = join(codegenDir, compId);

  await outputDir(directory);

  const entryFilePath = join(srcDir, entryFileName);
  const HocEntrypoint = join(directory, `wrapper.tsx`);

  await writeFile(
    HocEntrypoint,
    outdent`
      import Component from '${normalizePath(entryFilePath)}';
      import { wrapComponent } from '${normalizePath(wrapComponentUrl)}';

      export const WrappedComponent = wrapComponent(Component);
    `
  );

  const entrypoint = join(directory, `entry.astro`);

  await writeFile(
    entrypoint,
    outdent`
      ---
      import { WrappedComponent } from '${normalizePath(HocEntrypoint)}';
      ---

      <html lang="en">
        <head></head>
        <body>
          <div>
            <WrappedComponent client:only="react" />
          </div>
        </body>
      </html>
    `
  );
}
