import type {
  DevCenterBackofficeExtensionMenuItem,
  DevCenterBackofficeExtensionWidget,
  DevCenterBackofficeModal,
  DevCenterBackofficePage,
} from '@wix/dev-center-schemas';
import { appendToDevServerUrl } from '@wix/vite-utils';
import type { SupportedExtension } from '../types.js';

export function createExtensionManifest(
  extension: SupportedExtension,
  baseUrl: string
) {
  const iframeUrl = appendToDevServerUrl(
    baseUrl,
    `/_wix/extensions/backoffice/${extension.options.id}`
  );

  switch (extension.type) {
    case 'BackofficeExtensionMenuPlugin': {
      return {
        compId: extension.options.id,
        compType: 'BACK_OFFICE_EXTENSION_MENU_ITEM',
        compData: {
          backOfficeExtensionMenuItem: {
            iframeUrl,
          },
        },
      } satisfies DevCenterBackofficeExtensionMenuItem;
    }
    case 'BackofficeExtensionWidget': {
      return {
        compId: extension.options.id,
        compType: 'BACK_OFFICE_EXTENSION_WIDGET',
        compData: {
          backOfficeExtensionWidget: {
            iframeUrl,
          },
        },
      } satisfies DevCenterBackofficeExtensionWidget;
    }
    case 'BackofficeModal': {
      return {
        compId: extension.options.id,
        compType: 'BACK_OFFICE_MODAL',
        compData: {
          backOfficeModal: {
            iframeUrl,
          },
        },
      } satisfies DevCenterBackofficeModal;
    }
    case 'BackofficePage': {
      return {
        compId: extension.options.id,
        compType: 'BACK_OFFICE_PAGE',
        compData: {
          backOfficePage: {
            hostingPlatform: 'BUSINESS_MANAGER',
            iframeUrl,
            routePath: extension.options.routePath,
            title: extension.options.title,
          },
        },
      } satisfies DevCenterBackofficePage;
    }
  }
}
