import type { ViteDevServer } from 'vite';
import type { DevMetadata } from '../types.js';
import { serverUrlPlaceholder, staticsUrlPlaceholder } from './placeholders.js';

export function createDevMetadataMiddleware(server: ViteDevServer) {
  server.middlewares.use(async (req, res, next) => {
    if (req.url === '/_wix/dev-metadata.json') {
      const devMetadata: DevMetadata = {
        serverUrlPlaceholder,
        staticsUrlPlaceholder,
      };

      res.setHeader('Content-Type', 'application/json');
      res.end(JSON.stringify(devMetadata));

      return;
    }

    next();
  });
}
