import type {
  DevCenterCustomElementBase,
  DevCenterCustomElementBehaviors,
  DevCenterCustomElementDependencies,
  DevCenterCustomElementInstallation,
  DevCenterCustomElementPresets,
  DevCenterCustomElementSize,
} from '@wix/dev-center-schemas';

export interface CustomElement {
  type: 'CustomElement';
  options: Options;
}

interface Options {
  id: string;
  name: string;
  base: DevCenterCustomElementBase;
  behaviors: DevCenterCustomElementBehaviors;
  dependencies: DevCenterCustomElementDependencies;
  element: string;
  installation: DevCenterCustomElementInstallation;
  presets?: DevCenterCustomElementPresets;
  size: DevCenterCustomElementSize;
  tagName: string;
}

export function customElement(options: Options): CustomElement {
  return {
    type: 'CustomElement',
    options,
  };
}
