import { join } from 'node:path';
import { fileURLToPath } from 'node:url';
import type { CreateExtensionViteConfig } from '@wix/astro-core';
import type { EmbeddedScript } from '../builders.js';
import { viteSdkSupportPlugin } from './viteSdkSupportPlugin.js';

const setupContextUrl = fileURLToPath(
  new URL(
    '../dependencies/astro-embedded-scripts-extensions/browser-runtime/setupContext.js',
    import.meta.url
  )
);

export function getViteConfig({
  createExtensionViteConfig,
  extensions,
  srcDir,
}: {
  createExtensionViteConfig: CreateExtensionViteConfig;
  extensions: EmbeddedScript[];
  srcDir: string;
}) {
  const input = [
    setupContextUrl,
    ...extensions.map((embeddedScript) =>
      join(srcDir, embeddedScript.options.source)
    ),
  ];

  return createExtensionViteConfig({
    name: 'embedded-scripts',
    config: {
      build: {
        lib: {
          entry: setupContextUrl,
          formats: ['es'],
        },
        rollupOptions: {
          input,
        },
      },
      plugins: [viteSdkSupportPlugin()],
    },
  });
}
