import { ok } from 'node:assert';
import { relative } from 'node:path';
import { fileURLToPath } from 'node:url';
import type { WixIntegration } from '@wix/astro-core';
import type { DevCenterSiteComponent } from '@wix/dev-center-schemas';
import type { Rollup, ViteDevServer } from 'vite';
import { viteBuildOutputToRollupOutputs } from '@wix/vite-utils';
import { build, createServer } from 'vite';
import type { SupportedExtension } from './types.js';
import { getComponentResources } from './utils/getComponentResources.js';
import { getSupportedExtensions } from './utils/getSupportedExtensions.js';
import { getViteConfig } from './vite/getConfig.js';

export const createIntegration = (): WixIntegration => {
  let srcDir: null | string = null;
  let extensions: null | SupportedExtension[] = null;

  let customBuildOutput: null | Rollup.RollupOutput = null;
  let customDevServer: null | ViteDevServer = null;

  const getExtensions = () => {
    ok(extensions);
    return extensions;
  };

  return {
    name: '@wix/astro/site-components',
    hooks: {
      'astro:config:setup'({ config }) {
        srcDir = relative(
          fileURLToPath(config.root),
          fileURLToPath(config.srcDir)
        );
      },
      'wix:model:setup'({ model }) {
        ok(srcDir != null);
        extensions = getSupportedExtensions(model.extensions, { srcDir });
      },
      'wix:model:changed'({ model }) {
        ok(srcDir != null);
        extensions = getSupportedExtensions(model.extensions, { srcDir });
      },
      async 'wix:server:start'({
        createExtensionViteConfig,
        setupExtensionProxy,
      }) {
        ok(extensions);

        customDevServer = await createServer(
          getViteConfig({ createExtensionViteConfig, getExtensions })
        );

        await customDevServer.listen();

        setupExtensionProxy(customDevServer);
      },
      async 'astro:server:done'() {
        ok(customDevServer);
        await customDevServer.close();
      },
      async 'wix:build:setup'({ createExtensionViteConfig }) {
        ok(extensions);

        if (extensions.length === 0) {
          return;
        }

        ok(srcDir != null);

        const buildOutput = await build(
          getViteConfig({ createExtensionViteConfig, getExtensions })
        );

        const [clientOutput] = viteBuildOutputToRollupOutputs(buildOutput);
        ok(clientOutput);

        customBuildOutput = clientOutput;
      },
      async 'wix:app-manifest:setup'({
        staticsUrlPlaceholder,
        updateAppManifest,
      }) {
        ok(extensions);
        ok(srcDir != null);

        const components: DevCenterSiteComponent[] = [];

        for (const extension of extensions) {
          const resources = getComponentResources({
            baseUrl: staticsUrlPlaceholder,
            buildOutput: customBuildOutput,
            devServer: customDevServer,
            extension,
          });

          components.push({
            compId: extension.options.id,
            compType: 'EDITOR_REACT_COMPONENT',
            compData: {
              editorReactComponent: {
                type: extension.options.type,
                description: extension.options.description,
                editorElement: extension.options.editorElement,
                resources,
              },
            },
          });
        }

        updateAppManifest({ components });
      },
    },
  };
};
