import type { WixIntegration } from '@wix/astro-core';
import type { DevCenterSitePlugin } from '@wix/dev-center-schemas';
import { filterSupportedExtensions } from './utils/filterSupportedExtensions.js';

export const createIntegration = (): WixIntegration => {
  return {
    name: '@wix/astro/site-plugins',
    hooks: {
      async 'wix:app-manifest:setup'({ model, updateAppManifest }) {
        const components = model.extensions
          .filter(filterSupportedExtensions)
          .map((sitePlugin) => {
            return {
              compId: sitePlugin.options.id,
              compName: sitePlugin.options.name,
              compType: 'WIDGET_PLUGIN',
              compData: {
                widgetPlugin: {
                  installation: sitePlugin.options.installation,
                  marketData: sitePlugin.options.marketData,
                  placements: sitePlugin.options.placements,
                  referenceComponentId: sitePlugin.options.referenceComponentId,
                },
              },
            } satisfies DevCenterSitePlugin;
          });

        updateAppManifest({
          components,
        });
      },
    },
  };
};
