import EventEmitter from 'node:events';
import type { AstroIntegrationLogger } from 'astro';
import type { UserConfig, ViteDevServer } from 'vite';
import { createServer, mergeConfig } from 'vite';
import type { App } from '../builders/app.js';
import type { BaseExtension } from '../types.js';
import { loadEnvVars } from './loadEnvVars.js';

interface EventMap {
  change: [];
}

export class Model extends EventEmitter<EventMap> {
  public appId: string;
  public env: Record<string, string>;
  public extensions: BaseExtension[];

  private server: ViteDevServer;

  constructor({
    appId,
    env,
    extensions,
    server,
  }: {
    appId: string;
    env: Record<string, string>;
    extensions: BaseExtension[];
    server: ViteDevServer;
  }) {
    super();

    this.appId = appId;
    this.env = env;
    this.extensions = extensions;
    this.server = server;

    this.server.watcher.on('change', async () => {
      this.extensions = await loadExtensions(this.server);
      this.emit('change');
    });
  }
}

export async function createModel({
  logger,
  rootDir,
  srcDir,
}: {
  logger: AstroIntegrationLogger;
  rootDir: string;
  srcDir: string;
}): Promise<Model> {
  const { appId, env } = loadEnvVars({ logger, rootDir });

  const server = await getTempViteServer({
    viteConfig: {
      root: srcDir,
    },
  });

  const extensions = await loadExtensions(server);

  const model = new Model({
    appId,
    env,
    extensions,
    server,
  });

  return model;
}

async function getTempViteServer({
  viteConfig,
}: {
  viteConfig: UserConfig;
}): Promise<ViteDevServer> {
  const tempViteServer = await createServer(
    mergeConfig(viteConfig, {
      logLevel: 'silent',
      optimizeDeps: { noDiscovery: true },
      server: {
        middlewareMode: true,
        ws: false,
      },
      ssr: { external: [] },
    })
  );

  return tempViteServer;
}

async function loadExtensions(server: ViteDevServer) {
  const mod = (await server
    .ssrLoadModule('/extensions.ts')
    .catch((error: unknown) => {
      if (
        error != null &&
        typeof error === 'object' &&
        'code' in error &&
        error.code === 'ERR_LOAD_URL'
      ) {
        return null;
      }

      throw error;
    })) as null | {
    default: App;
  };

  if (mod) {
    // @ts-expect-error hidden types
    return mod.default.extensions;
  }

  return [];
}
