import { ok } from 'node:assert';
import { fileURLToPath } from 'node:url';
import type { WixIntegration } from '@wix/astro-core';
import { clearDir } from '@wix/fs-utils';
import { buildComponents } from './build-components/buildComponents.js';
import { createExtensionManifest } from './utils/createExtensionManifest.js';
import { filterSupportedExtensions } from './utils/filterSupportedExtensions.js';
import { removeUnusedCodegenFiles } from './utils/removeUnusedCodegenFiles.js';

export const createIntegration = (): WixIntegration => {
  let srcDir: null | string = null;
  let codegenDir: null | string = null;

  return {
    name: '@wix/astro/backend-extensions',
    hooks: {
      async 'astro:config:setup'({ config, createCodegenDir, injectRoute }) {
        srcDir = fileURLToPath(config.srcDir);
        codegenDir = fileURLToPath(createCodegenDir());

        injectRoute({
          entrypoint: new URL(
            '../dependencies/astro-backend-extensions/backend-runtime/routes/webhooks.js',
            import.meta.url
          ),
          pattern: `/_wix/extensions/webhooks/[compId]`,
          prerender: false,
        });

        injectRoute({
          entrypoint: new URL(
            '../dependencies/astro-backend-extensions/backend-runtime/routes/servicePlugins.js',
            import.meta.url
          ),
          pattern: `/_wix/extensions/service-plugins/[compId]/[...path]`,
          prerender: false,
        });
      },
      async 'wix:model:setup'({ model }) {
        ok(srcDir != null);
        ok(codegenDir != null);

        const extensions = model.extensions.filter(filterSupportedExtensions);

        await clearDir(codegenDir);
        await buildComponents({ codegenDir, extensions, srcDir });
      },
      async 'wix:model:changed'({ model }) {
        ok(srcDir != null);
        ok(codegenDir != null);

        const extensions = model.extensions.filter(filterSupportedExtensions);

        await removeUnusedCodegenFiles({ codegenDir, extensions });
        await buildComponents({ codegenDir, extensions, srcDir });
      },
      'wix:app-manifest:setup'({
        model,
        serverUrlPlaceholder,
        updateAppManifest,
      }) {
        const extensions = model.extensions.filter(filterSupportedExtensions);

        updateAppManifest({
          components: extensions.map((extension) =>
            createExtensionManifest(extension, serverUrlPlaceholder)
          ),
        });
      },
    },
  };
};
