import type { BaseExtension } from '@wix/astro-core';
import type { SupportedExtension } from '../types.js';

export function filterSupportedExtensions(
  extension: BaseExtension
): extension is SupportedExtension {
  return (
    extension.type === 'BackofficeExtensionMenuPlugin' ||
    extension.type === 'BackofficeExtensionWidget' ||
    extension.type === 'BackofficeModal' ||
    extension.type === 'BackofficePage'
  );
}
