import { ok } from 'node:assert';
import { join } from 'node:path';
import type { Rollup, ViteDevServer } from 'vite';
import { normalizePath } from '@wix/fs-utils';

export function getAssetUrl({
  buildOutput,
  fileName,
  viteDevServer,
}: {
  buildOutput: null | Rollup.RollupOutput;
  fileName: string;
  viteDevServer: null | ViteDevServer;
}) {
  if (buildOutput) {
    return getChunkUrlForBuildOutput({ buildOutput, fileName });
  }

  if (viteDevServer) {
    return getAssetUrlForDevServer({ fileName, viteDevServer });
  }

  throw new Error('Unknown error: no build output or vite dev server');
}

function getAssetUrlForDevServer({
  fileName,
  viteDevServer,
}: {
  fileName: string;
  viteDevServer: ViteDevServer;
}) {
  return join(viteDevServer.config.base, '@id', `${fileName}?import`);
}

function getChunkUrlForBuildOutput({
  buildOutput,
  fileName,
}: {
  buildOutput: Rollup.RollupOutput;
  fileName: string;
}): string {
  const asset = buildOutput.output
    .filter((x) => x.type === 'chunk')
    .find((x) => x.facadeModuleId === normalizePath(fileName));

  ok(asset);

  return asset.fileName;
}
