import type { CreateExtensionViteConfig } from '@wix/astro-core';
import { viteExternalsPlugin } from 'vite-plugin-externals';
import type { CustomElement } from '../builders/customElement.js';
import { viteWrapCustomElementPlugin } from './viteWrapCustomElementPlugin.js';

export function getViteConfig({
  createExtensionViteConfig,
  getExtensions,
  srcDir,
}: {
  createExtensionViteConfig: CreateExtensionViteConfig;
  getExtensions: () => CustomElement[];
  srcDir: string;
}) {
  const input = getExtensions().map(
    (extension) => `${extension.options.id}.js`
  );

  return createExtensionViteConfig({
    name: 'custom-elements',
    config: {
      build: {
        rollupOptions: {
          input,
        },
      },
      plugins: [
        viteWrapCustomElementPlugin({ getExtensions, srcDir }),
        viteExternalsPlugin({
          react: 'React',
          'react-dom': 'ReactDOM',
        }),
      ],
    },
  });
}
