import type { BaseExtension } from '@wix/astro-core';
import type { SupportedExtension } from '../types.js';

export function filterSupportedExtensions(
  extension: BaseExtension
): extension is SupportedExtension {
  return (
    extension.type === 'EcomAdditionalFees' ||
    extension.type === 'EcomDiscountsTrigger' ||
    extension.type === 'EcomGiftCardsProvider' ||
    extension.type === 'EcomPaymentSettings' ||
    extension.type === 'EcomShippingRates' ||
    extension.type === 'EcomValidations' ||
    extension.type === 'Webhook'
  );
}
