import type EventEmitter from 'node:events';
import type { IncomingMessage } from 'node:http';
import type { ViteDevServer } from 'vite';
import { getLocalDevServerUrl } from '@wix/vite-utils';
import { createProxy } from 'http-proxy-3';

export const EXTENSION_PROXY_BASE = '/_wix_proxy';

export function createExtensionsProxy(
  astroServer: ViteDevServer,
  devServersToProxy: Set<ViteDevServer>
) {
  const proxy = createProxy({
    preserveHeaderKeyCase: true,
    proxyTimeout: 60_000,
    timeout: 60_000,
  });

  proxy.on('proxyReq', (proxyReq) => {
    const connection = proxyReq.getHeader('Connection');
    if (connection === 'close') {
      proxyReq.setHeader('Connection', 'keep-alive');
    }
  });

  proxy.on('error', (_err, _req, res) => {
    // When it is ws proxy, res is net.Socket
    if ('req' in res) {
      if (!res.headersSent && !res.writableEnded) {
        res.removeHeader('Content-Length');
        res
          .writeHead(500, {
            'Content-Type': 'text/plain',
          })
          .end(
            'Could not connect to the target server through the proxy. Make sure the server is up and reachable.'
          );
      }
    } else {
      res.end();
    }
  });

  astroServer.middlewares.use((req, res, next) => {
    if (req.url?.startsWith(EXTENSION_PROXY_BASE)) {
      for (const devServer of devServersToProxy) {
        if (req.url.startsWith(devServer.config.base)) {
          return proxy.web(req, res, {
            changeOrigin: true,
            target: getLocalDevServerRootUrl(devServer),
          });
        }
      }
    }
    next();
  });

  astroServer.httpServer?.on(
    'upgrade',
    (req: IncomingMessage, socket: EventEmitter, head: unknown) => {
      if (req.url?.startsWith(EXTENSION_PROXY_BASE)) {
        for (const devServer of devServersToProxy) {
          if (req.url.startsWith(devServer.config.base)) {
            proxy.ws(req, socket, head, {
              changeOrigin: true,
              target: getLocalDevServerRootUrl(devServer),
            });
            return;
          }
        }
      }
    }
  );
}

function getLocalDevServerRootUrl(server: ViteDevServer) {
  const url = new URL(getLocalDevServerUrl(server));
  url.pathname = '/';
  return url.toString();
}
