import type {
  DevCenterEcomAdditionalFees,
  DevCenterEcomDiscountsTrigger,
  DevCenterEcomGiftCardsProvider,
  DevCenterEcomPaymentSettings,
  DevCenterEcomShippingRates,
  DevCenterEcomValidations,
  DevCenterWebhook,
} from '@wix/dev-center-schemas';
import { appendToDevServerUrl } from '@wix/vite-utils';
import type { SupportedExtension } from '../types.js';

export function createExtensionManifest(
  extension: SupportedExtension,
  baseUrl: string
) {
  switch (extension.type) {
    case 'EcomAdditionalFees': {
      return {
        compId: extension.options.id,
        compType: 'ECOM_ADDITIONAL_FEES',
        compData: {
          ecomAdditionalFees: {
            deploymentUri: appendToDevServerUrl(
              baseUrl,
              `/_wix/extensions/service-plugins/${extension.options.id}`
            ),
          },
        },
      } satisfies DevCenterEcomAdditionalFees;
    }
    case 'EcomDiscountsTrigger': {
      return {
        compId: extension.options.id,
        compType: 'ECOM_DISCOUNTS_TRIGGER',
        compData: {
          ecomDiscountsTrigger: {
            deploymentUri: appendToDevServerUrl(
              baseUrl,
              `/_wix/extensions/service-plugins/${extension.options.id}`
            ),
          },
        },
      } satisfies DevCenterEcomDiscountsTrigger;
    }
    case 'EcomGiftCardsProvider': {
      return {
        compId: extension.options.id,
        compType: 'GIFT_CARDS_PROVIDER',
        compData: {
          giftCardsProvider: {
            deploymentUri: appendToDevServerUrl(
              baseUrl,
              `/_wix/extensions/service-plugins/${extension.options.id}`
            ),
          },
        },
      } satisfies DevCenterEcomGiftCardsProvider;
    }
    case 'EcomPaymentSettings': {
      return {
        compId: extension.options.id,
        compType: 'ECOM_PAYMENT_SETTINGS',
        compData: {
          ecomPaymentSettings: {
            deploymentUri: appendToDevServerUrl(
              baseUrl,
              `/_wix/extensions/service-plugins/${extension.options.id}`
            ),
          },
        },
      } satisfies DevCenterEcomPaymentSettings;
    }
    case 'EcomShippingRates': {
      return {
        compId: extension.options.id,
        compType: 'ECOM_SHIPPING_RATES',
        compData: {
          ecomShippingRates: {
            name: extension.options.name,
            deploymentUri: appendToDevServerUrl(
              baseUrl,
              `/_wix/extensions/service-plugins/${extension.options.id}`
            ),
          },
        },
      } satisfies DevCenterEcomShippingRates;
    }
    case 'EcomValidations': {
      return {
        compId: extension.options.id,
        compType: 'ECOM_VALIDATIONS',
        compData: {
          ecomValidations: {
            deploymentUri: appendToDevServerUrl(
              baseUrl,
              `/_wix/extensions/service-plugins/${extension.options.id}`
            ),
          },
        },
      } satisfies DevCenterEcomValidations;
    }
    case 'Webhook': {
      return {
        compId: extension.options.id,
        compType: 'WEBHOOK',
        compData: {
          webhook: {
            callbackUrl: appendToDevServerUrl(
              baseUrl,
              `/_wix/extensions/webhooks/${extension.options.id}`
            ),
            webhookSlug: extension.options.slug,
          },
        },
      } satisfies DevCenterWebhook;
    }
  }
}
