import { writeFile } from 'node:fs/promises';
import { join } from 'node:path';
import { normalizePath, outputDir } from '@wix/fs-utils';
import { outdent } from 'outdent';

const setupWebhookUrl = new URL(
  '../dependencies/astro-backend-extensions/backend-runtime/runtime/setupWebhook.js',
  import.meta.url
);

export async function buildWebhookComponent({
  compId,
  codegenDir,
  entryFileName,
  srcDir,
}: {
  compId: string;
  codegenDir: string;
  entryFileName: string;
  srcDir: string;
}): Promise<void> {
  const directory = join(codegenDir, compId);

  await outputDir(directory);

  const entrypoint = join(directory, `entry.ts`);
  const entryFilePath = join(srcDir, entryFileName);

  await writeFile(
    entrypoint,
    outdent`
      import { setupWebhook } from '${normalizePath(setupWebhookUrl)}';

      export default await setupWebhook(
        () => import('${normalizePath(entryFilePath)}'),
      );
    `
  );
}
