import type { BaseExtension } from '@wix/astro-core';

export interface SiteComponent extends BaseExtension {
  type: 'SiteComponent';
  options: Options;
}

interface Options {
  id: string;
  type: string;
  description: string;
  editorElement: {
    [key: string]: unknown;
    displayName: string;
    selector: string;
  };
  resources: {
    client: {
      component: string;
    };
  };
}

export function siteComponent(options: Options): SiteComponent {
  return {
    type: 'SiteComponent',
    options,
  };
}
