import { join, parse } from 'node:path';
import type { PluginOption } from 'vite';
import { normalizePath } from '@wix/fs-utils';
import { outdent } from 'outdent';
import type { CustomElement } from '../builders/customElement.js';

const runtimeUrl = new URL(
  '../dependencies/astro-custom-elements-extensions/browser-runtime/index.js',
  import.meta.url
);

export function viteWrapCustomElementPlugin({
  getExtensions,
  srcDir,
}: {
  getExtensions: () => CustomElement[];
  srcDir: string;
}): PluginOption {
  const getExtensionForModuleId = (id: string) => {
    const { name: baseId } = parse(id);

    const result = getExtensions().find(
      (extension) => extension.options.id === baseId
    );

    return result;
  };

  return {
    name: 'vite-wrap-custom-element-plugin',
    load: {
      handler(id) {
        const extension = getExtensionForModuleId(id);

        if (extension) {
          const filePath = join(srcDir, extension.options.element);

          return outdent`
            import CustomElement from '${normalizePath(filePath)}';
            import { wrapCustomElement, handleHmr } from '${normalizePath(runtimeUrl)}';

            if (import.meta.hot) {
              import.meta.hot.accept(() => {});
              import.meta.hot.accept(
                '${normalizePath(filePath)}',
                function({ default: NewCustomElement }) {
                  handleHmr({
                    customElement: NewCustomElement,
                    tagName: '${extension.options.tagName}',
                  });
                }
              );
            }

            wrapCustomElement({
              customElement: CustomElement,
              tagName: '${extension.options.tagName}',
            });
          `;
        }

        return null;
      },
      order: 'pre',
    },
    resolveId(id) {
      const extension = getExtensionForModuleId(id);

      if (extension) {
        return id;
      }

      return null;
    },
  };
}
