import { ok } from 'node:assert';
import type { Rollup, ViteDevServer } from 'vite';
import { normalizePath } from '@wix/fs-utils';
import { appendToDevServerUrl, getLocalDevServerUrl } from '@wix/vite-utils';

export async function getAssetContent({
  buildOutput,
  fileName,
  viteDevServer,
}: {
  buildOutput: null | Rollup.RollupOutput;
  fileName: string;
  viteDevServer: null | ViteDevServer;
}): Promise<string> {
  if (buildOutput) {
    return getAssetContentFromBuildOutput({ buildOutput, fileName });
  }

  if (viteDevServer) {
    return fetchAssetContentFromDevServer({ fileName, viteDevServer });
  }

  throw new Error('Unknown error: no build output or vite dev server');
}

async function fetchAssetContentFromDevServer({
  fileName,
  viteDevServer,
}: {
  fileName: string;
  viteDevServer: ViteDevServer;
}): Promise<string> {
  const devServerUrl = getLocalDevServerUrl(viteDevServer);

  const response = await fetch(appendToDevServerUrl(devServerUrl, fileName));
  const content = await response.text();

  return content;
}

function getAssetContentFromBuildOutput({
  buildOutput,
  fileName,
}: {
  buildOutput: Rollup.RollupOutput;
  fileName: string;
}): string {
  const asset = buildOutput.output
    .filter((x) => x.type === 'asset')
    .find((x) => x.fileName === normalizePath(fileName));

  ok(asset);

  return asset.source.toString();
}
