import { join } from 'node:path';
import type { SupportedExtension } from '../types.js';
import { buildBackofficeComponent } from './buildBackofficeComponent.js';

export async function buildComponents({
  codegenDir,
  extensions,
  srcDir,
}: {
  codegenDir: string;
  extensions: SupportedExtension[];
  srcDir: string;
}): Promise<void> {
  const backofficeCodegenDir = join(codegenDir, 'backoffice');

  for (const extension of extensions) {
    switch (extension.type) {
      case 'BackofficeExtensionMenuPlugin': {
        // configuration only

        break;
      }
      case 'BackofficeExtensionWidget': {
        await buildBackofficeComponent({
          compId: extension.options.id,
          codegenDir: backofficeCodegenDir,
          entryFileName: extension.options.component,
          srcDir,
        });

        break;
      }
      case 'BackofficeModal': {
        await buildBackofficeComponent({
          compId: extension.options.id,
          codegenDir: backofficeCodegenDir,
          entryFileName: extension.options.component,
          srcDir,
        });

        break;
      }
      case 'BackofficePage': {
        await buildBackofficeComponent({
          compId: extension.options.id,
          codegenDir: backofficeCodegenDir,
          entryFileName: extension.options.component,
          srcDir,
        });

        break;
      }
    }
  }
}
