import type { ViteDevServer } from 'vite';
import type { WixIntegration } from '../types.js';
import type { Model } from './createModel.js';
import { generateAppManifest } from './generateAppManifest.js';

export function createAppManifestMiddleware(
  server: ViteDevServer,
  integrations: WixIntegration[],
  model: Model
) {
  server.middlewares.use(async (req, res, next) => {
    if (req.url === '/_wix/app-manifest.json') {
      const appManifest = await generateAppManifest({
        integrations,
        model,
      });

      res.setHeader('Content-Type', 'application/json');
      res.end(appManifest);

      return;
    }

    next();
  });
}
