export interface BackofficeExtensionMenuPlugin {
  type: 'BackofficeExtensionMenuPlugin';
  options: Options;
}

interface Options {
  id: string;
  component: string;
}

export function backofficeExtensionMenuPlugin(
  options: Options
): BackofficeExtensionMenuPlugin {
  return {
    type: 'BackofficeExtensionMenuPlugin',
    options,
  };
}
