import type { DevCenterAppManifest } from '@wix/dev-center-schemas';
import { toJsonString } from '@wix/fs-utils';
import type { GenericExtension } from '../builders/generic-extension.js';
import type { WixIntegration } from '../types.js';
import type { Model } from './createModel.js';
import { serverUrlPlaceholder, staticsUrlPlaceholder } from './placeholders.js';

export async function generateAppManifest({
  integrations,
  model,
}: {
  integrations: WixIntegration[];
  model: Model;
}): Promise<string> {
  const unknownComponents = model.extensions
    .filter(
      (extension): extension is GenericExtension =>
        extension.type === 'GenericExtension'
    )
    .map((extension) => extension.options);

  let appManifest = {
    appId: model.appId,
    components: unknownComponents,
  } satisfies DevCenterAppManifest;

  for (const integration of integrations) {
    if (integration.hooks['wix:app-manifest:setup']) {
      await integration.hooks['wix:app-manifest:setup']({
        model,
        serverUrlPlaceholder,
        staticsUrlPlaceholder,
        updateAppManifest: (newAppManifest) => {
          appManifest = {
            ...appManifest,
            ...newAppManifest,
            components: [
              ...appManifest.components,
              ...(newAppManifest.components ?? []),
            ],
          };
        },
      });
    }
  }

  const rawAppManifest = toJsonString(appManifest).replaceAll(
    '{{BASE_URL}}',
    staticsUrlPlaceholder
  );

  return rawAppManifest;
}
