import type { BaseExtension } from '@wix/astro-core';
import type {
  DevCenterSitePluginInstallation,
  DevCenterSitePluginMarketData,
  DevCenterSitePluginPlacements,
} from '@wix/dev-center-schemas';

export interface SitePlugin extends BaseExtension {
  type: 'SitePlugin';
  options: Options;
}

interface Options {
  id: string;
  name?: string;
  installation: DevCenterSitePluginInstallation;
  marketData: DevCenterSitePluginMarketData;
  placements: DevCenterSitePluginPlacements[];
  referenceComponentId: string;
}

export function sitePlugin(options: Options): SitePlugin {
  return {
    type: 'SitePlugin',
    options,
  };
}
