import { ok } from 'node:assert';
import { join } from 'node:path';
import { fileURLToPath } from 'node:url';
import type { PluginOption, Rollup, ViteDevServer } from 'vite';
import { normalizePath } from '@wix/fs-utils';
import { outdent } from 'outdent';

const setupContextUrl = new URL(
  '../dependencies/astro-embedded-scripts-extensions/browser-runtime/setupContext.js',
  import.meta.url
);

export function viteSdkSupportPlugin(): PluginOption {
  return {
    name: 'vite-sdk-support-plugin',
    transformIndexHtml: {
      handler(html, { bundle, server }) {
        const scriptPath = resolveRuntimeScriptPath({ bundle, server });

        return outdent`
          <script type="module" accesstoken="true" crossorigin src="${scriptPath}"></script>
          ${html}
        `;
      },
      order: 'post',
    },
  };
}

function resolveRuntimeScriptPath({
  bundle,
  server,
}: {
  bundle?: Rollup.OutputBundle;
  server?: ViteDevServer;
}) {
  if (server) {
    return join(server.config.base, '/@fs', fileURLToPath(setupContextUrl));
  }

  if (bundle) {
    const runtimeChunk = Object.values(bundle).find(
      (chunk) =>
        chunk.type === 'chunk' &&
        chunk.facadeModuleId === normalizePath(setupContextUrl)
    );
    ok(runtimeChunk);

    return `/${runtimeChunk.fileName}`;
  }

  throw new Error(
    'Unexpected error: either `bundle` or `server` should be available'
  );
}
