import type { CreateExtensionViteConfig } from '@wix/astro-core';
import externalize from 'vite-plugin-externalize-dependencies';
import type { SupportedExtension } from '../types.js';
import {
  getVirtualModuleIdForComponent,
  siteComponentPlugin,
} from './siteComponentPlugin.js';

const externalDeps = ['react', 'react-dom', '@wix/services-manager-react'];

export function getViteConfig({
  createExtensionViteConfig,
  getExtensions,
}: {
  createExtensionViteConfig: CreateExtensionViteConfig;
  getExtensions: () => SupportedExtension[];
}) {
  const extensions = getExtensions();
  const input = extensions.map(
    (extension) =>
      [
        `component-${extension.options.id}`,
        getVirtualModuleIdForComponent(extension.options.id),
      ] as const
  );

  return createExtensionViteConfig({
    name: 'site-components',
    config: {
      build: {
        rollupOptions: {
          external: externalDeps,
          input: Object.fromEntries(input),
          // TODO: investigate lib mode instead
          preserveEntrySignatures: 'strict',
        },
      },
      optimizeDeps: {
        entries: extensions.map((ext) => ext.componentRelativePath),
      },
      plugins: [
        externalize({ externals: externalDeps }),
        siteComponentPlugin({ getExtensions }),
      ],
    },
  });
}
