import { ok } from 'node:assert';
import { createHash } from 'node:crypto';
import { join } from 'node:path';
import type { InlineConfig, ResolvedConfig } from 'vite';
import { mergeConfig } from 'vite';
import type { CreateExtensionViteConfig } from '../types.js';
import { EXTENSION_PROXY_BASE } from './createExtensionProxy.js';

export function createExtensionViteConfigFactory({
  command,
  config: astroConfig,
  env,
}: {
  command: 'build' | 'serve';
  config: InlineConfig | ResolvedConfig;
  env: Record<string, string>;
}) {
  let initialServerPort = 6001;

  return (integrationName: string): CreateExtensionViteConfig => {
    return ({ name: extensionName, config: extensionConfig }) => {
      ok(astroConfig.build?.outDir != null);

      const hash = createShortHash(integrationName);
      const extensionId = `${hash}-${extensionName}`;
      const outputDir = `_wix_${extensionId}`;

      const commandConfig: InlineConfig = {};
      if (command === 'serve') {
        commandConfig.base = `${EXTENSION_PROXY_BASE}/${extensionId}/`;
      }

      return mergeConfig(
        {
          build: {
            assetsDir: outputDir,
            emptyOutDir: false,
            outDir: astroConfig.build.outDir,
            rollupOptions: {
              output: {
                assetFileNames: join(outputDir, `[name]-[hash][extname]`),
                chunkFileNames: join(outputDir, `[name]-[hash].js`),
                entryFileNames: join(outputDir, `[name]-[hash].js`),
                format: 'esm',
              },
            },
            target: astroConfig.build.target,
          },
          cacheDir: join('node_modules', '.vite-wix', extensionId),
          clearScreen: false,
          configFile: false,
          customLogger: astroConfig.customLogger,
          define: {
            'import.meta.env.WIX_CLIENT_ID': JSON.stringify(env.WIX_CLIENT_ID),
          },
          root: astroConfig.root,
          server: {
            cors: true,
            port: initialServerPort++,
          },
          ...commandConfig,
        } satisfies InlineConfig,
        extensionConfig
      );
    };
  };
}

function createShortHash(str: string) {
  return createHash('sha256').update(str).digest('hex').substring(0, 8);
}
