import type { BaseExtension } from '@wix/astro-core';

export interface EmbeddedScript extends BaseExtension {
  type: 'EmbeddedScript';
  options: Options;
}

interface Options {
  id: string;
  name: string;
  placement: 'BODY_END' | 'BODY_START' | 'HEAD' | 'NONE_PLACEMENT';
  scriptType:
    | 'ADVERTISING'
    | 'ANALYTICS'
    | 'ESSENTIAL'
    | 'FUNCTIONAL'
    | 'UNKNOWN';
  source: string;
}

export function embeddedScript(options: Options): EmbeddedScript {
  return {
    type: 'EmbeddedScript',
    options,
  };
}
