import { ok } from 'node:assert';
import { join } from 'node:path';
import { fileURLToPath } from 'node:url';
import type { AstroConfig, AstroIntegration } from 'astro';
import type { ViteDevServer } from 'vite';
import { writeFile, writeJson } from '@wix/fs-utils';
import { envField, passthroughImageService } from 'astro/config';
import type { WixIntegration } from './types.js';
import type { Model } from './utils/createModel.js';
import { patchGlobal } from './plugins/patchGlobal.js';
import { createAppManifestMiddleware } from './utils/createAppManifestMiddleware.js';
import { createDevMetadataMiddleware } from './utils/createDevMetadataMiddleware.js';
import {
  createExtensionsProxy,
  EXTENSION_PROXY_BASE,
} from './utils/createExtensionProxy.js';
import { createExtensionViteConfigFactory } from './utils/createExtensionViteConfigFactory.js';
import { createModel } from './utils/createModel.js';
import { generateAppManifest } from './utils/generateAppManifest.js';
import { generateBuildMetadata } from './utils/generateBuildMetadata.js';

export const createIntegration = ({
  integrations,
}: {
  integrations: WixIntegration[];
}): AstroIntegration => {
  let config: AstroConfig;
  let model: Model | null;
  let astroServer: null | ViteDevServer;
  const devServersToProxy = new Set<ViteDevServer>();

  return {
    name: '@wix/astro/core',
    hooks: {
      async 'astro:config:setup'({
        config: astroConfig,
        logger,
        updateConfig,
      }) {
        config = astroConfig;

        const rootDir = fileURLToPath(astroConfig.root);
        const srcDir = fileURLToPath(astroConfig.srcDir);

        updateConfig({
          env: {
            schema: {
              WIX_CLIENT_ID: envField.string({
                access: 'public',
                context: 'client',
              }),
              WIX_CLIENT_INSTANCE_ID: envField.string({
                access: 'secret',
                context: 'server',
              }),
              WIX_CLIENT_PUBLIC_KEY: envField.string({
                access: 'secret',
                context: 'server',
              }),
              WIX_CLIENT_SECRET: envField.string({
                access: 'secret',
                context: 'server',
              }),
            },
          },
          image: {
            domains: ['static.wixstatic.com'],
            service: passthroughImageService(),
          },
          integrations,
          vite: {
            optimizeDeps: {
              include: [
                '@wix/sdk-runtime/context',
                '@wix/dashboard/internal',
                '@wix/sdk-context',
                '@wix/sdk',
                '@wix/sdk/auth/site-session',
                '@wix/headless-site',
                '@wix/stores',
                '@wix/site',
              ],
            },
            plugins: [patchGlobal()],
          },
        });

        model = await createModel({ logger, rootDir, srcDir });

        model.on('change', async () => {
          ok(model);

          for (const integration of integrations) {
            if (integration.hooks['wix:model:changed']) {
              await integration.hooks['wix:model:changed']({ model });
            }
          }
        });
      },
      async 'astro:config:done'({ config: { base } }) {
        ok(
          base === '/',
          `@wix/astro: The "base" configuration is not supported. Please remove or set "base" to "/". (Received: "${config.base}")`
        );
        ok(model);

        for (const integration of integrations) {
          if (integration.hooks['wix:model:setup']) {
            await integration.hooks['wix:model:setup']({ model });
          }
        }
      },
      async 'astro:server:setup'({ server }) {
        ok(model);

        if (server.httpServer) {
          astroServer = server;
        }

        createExtensionsProxy(server, devServersToProxy);
        createDevMetadataMiddleware(server);
        createAppManifestMiddleware(server, integrations, model);
      },
      async 'astro:server:start'() {
        ok(model);
        ok(astroServer);

        const createIntegrationViteConfig = createExtensionViteConfigFactory({
          command: 'serve',
          config: astroServer.config,
          env: model.env,
        });

        for (const integration of integrations) {
          if (integration.hooks['wix:server:start']) {
            await integration.hooks['wix:server:start']({
              createExtensionViteConfig: createIntegrationViteConfig(
                integration.name
              ),
              setupExtensionProxy: (server) => {
                ok(server.config.base.startsWith(EXTENSION_PROXY_BASE));
                devServersToProxy.add(server);
              },
            });
          }
        }
      },
      'astro:server:done'() {
        devServersToProxy.clear();
      },
      async 'astro:build:setup'({ target, vite }) {
        ok(model);

        if (target === 'server') {
          return;
        }

        const createIntegrationViteConfig = createExtensionViteConfigFactory({
          command: 'build',
          config: vite,
          env: model.env,
        });

        for (const integration of integrations) {
          if (integration.hooks['wix:build:setup']) {
            await integration.hooks['wix:build:setup']({
              createExtensionViteConfig: createIntegrationViteConfig(
                integration.name
              ),
            });
          }
        }
      },
      async 'astro:build:done'() {
        ok(model);

        const rootDir = fileURLToPath(config.root);
        const outDir = fileURLToPath(config.outDir);

        const buildMetadataPath = join(rootDir, '.wix/build-metadata.json');
        const appManifestPath = join(outDir, '_wix/app-manifest.json');

        const buildMetadata = await generateBuildMetadata(
          appManifestPath,
          config
        );
        const appManifest = await generateAppManifest({ integrations, model });

        await writeFile(appManifestPath, appManifest);
        await writeJson(buildMetadataPath, buildMetadata);
      },
    },
  };
};
