import { ok } from 'node:assert';
import type { DevCenterSiteComponentResources } from '@wix/dev-center-schemas';
import type { Rollup, ViteDevServer } from 'vite';
import { appendToDevServerUrl, getAssetUrlForModuleId } from '@wix/vite-utils';
import type { SiteComponent } from '../builders.js';
import { getVirtualModuleIdForComponent } from '../vite/siteComponentPlugin.js';

export function getComponentResources({
  baseUrl,
  buildOutput,
  devServer,
  extension,
}: {
  baseUrl: string;
  buildOutput: null | Rollup.RollupOutput;
  devServer: null | ViteDevServer;
  extension: SiteComponent;
}) {
  if (buildOutput) {
    return getComponentResourcesBuild({ baseUrl, buildOutput, extension });
  }

  if (devServer) {
    return getComponentResourcesDev({ baseUrl, devServer, extension });
  }

  throw new Error('Unknown error: no build output or vite dev server');
}

function getComponentResourcesBuild({
  baseUrl,
  buildOutput,
  extension,
}: {
  baseUrl: string;
  buildOutput: Rollup.RollupOutput;
  extension: SiteComponent;
}) {
  const moduleId = getVirtualModuleIdForComponent(extension.options.id);
  const chunk = buildOutput.output
    .filter((x) => x.type === 'chunk')
    .find((x) => x.facadeModuleId === moduleId);

  ok(chunk);

  const componentUrl = chunk.fileName;
  const cssUrl = chunk.viteMetadata?.importedCss.values().next().value;

  return {
    client: {
      componentUrl: appendToDevServerUrl(baseUrl, componentUrl),
      cssUrl:
        cssUrl != null ? appendToDevServerUrl(baseUrl, cssUrl) : undefined,
    },
  } satisfies DevCenterSiteComponentResources;
}

function getComponentResourcesDev({
  baseUrl,
  devServer,
  extension,
}: {
  baseUrl: string;
  devServer: ViteDevServer;
  extension: SiteComponent;
}) {
  const moduleId = getVirtualModuleIdForComponent(extension.options.id);

  return {
    client: {
      componentUrl: getAssetUrlForModuleId({ baseUrl, devServer, moduleId }),
    },
  } satisfies DevCenterSiteComponentResources;
}
