export interface BackofficeExtensionWidget {
  type: 'BackofficeExtensionWidget';
  options: Options;
}

interface Options {
  id: string;
  component: string;
}

export function backofficeExtensionWidget(
  options: Options
): BackofficeExtensionWidget {
  return {
    type: 'BackofficeExtensionWidget',
    options,
  };
}
