export interface BackofficePage {
  type: 'BackofficePage';
  options: Options;
}

interface Options {
  id: string;
  component: string;
  routePath: string;
  title: string;
}

export function backofficePage(options: Options): BackofficePage {
  return {
    type: 'BackofficePage',
    options,
  };
}
