import { ok } from 'node:assert';
import type { PluginOption, ResolvedConfig } from 'vite';
import { normalizePath } from '@wix/fs-utils';
import { outdent } from 'outdent';
import type { SiteComponentInternal, SupportedExtension } from '../types.js';

const browserRuntimePath = normalizePath(
  new URL(
    '../dependencies/astro-site-components-extensions/browser-runtime/hmr.js',
    import.meta.url
  )
);

const componentVirtualModuleIdPrefix = 'wix/component/';

export function getVirtualModuleIdForComponent(componentId: string): string {
  return `${componentVirtualModuleIdPrefix}${componentId}`;
}

export function siteComponentPlugin({
  getExtensions,
}: {
  getExtensions: () => SupportedExtension[];
}): PluginOption {
  let config: null | ResolvedConfig = null;

  return {
    name: 'wix:site-component-plugin',
    configResolved(resolvedConfig) {
      config = resolvedConfig;
    },
    load(id) {
      if (id.startsWith(componentVirtualModuleIdPrefix)) {
        const extensionId = id.slice(componentVirtualModuleIdPrefix.length);
        const extension = getExtensions().find(
          (ext): ext is SiteComponentInternal => ext.options.id === extensionId
        );

        if (extension) {
          ok(config);

          const componentPath = `/${normalizePath(extension.componentRelativePath)}`;

          if (config.command === 'build') {
            return getBuildTemplate(componentPath);
          }

          return getServeTemplate(componentPath);
        }
      }

      return null;
    },
    resolveId(id) {
      if (id.startsWith(componentVirtualModuleIdPrefix)) {
        return id;
      }

      return null;
    },
  };
}

function getBuildTemplate(componentPath: string) {
  return outdent`
    export { default } from '${componentPath}';
  `;
}

function getServeTemplate(componentPath: string) {
  return outdent`
    import { createComponentHmrWrapper } from '${browserRuntimePath}';
    import OriginalComponent from '${componentPath}';

    let WrappedComponent = OriginalComponent;

    if (import.meta.hot) {
      import.meta.hot.accept('${componentPath}', (newModule) => {
        if (newModule?.default) {
          import.meta.hot.data.setComponent?.(newModule?.default);
        }
      });

      WrappedComponent = createComponentHmrWrapper(OriginalComponent, import.meta.hot.data);
    }

    export default WrappedComponent;
  `;
}
