import { join } from 'node:path';
import type { BaseExtension } from '@wix/astro-core';
import type { SiteComponent } from '../builders.js';
import type { SupportedExtension } from '../types.js';

export function getSupportedExtensions(
  extensions: BaseExtension[],
  { srcDir }: { srcDir: string }
): SupportedExtension[] {
  const siteComponentExtensions = extensions.filter(filterSiteComponents);

  return siteComponentExtensions.map((extension) => ({
    ...extension,
    componentRelativePath: join(
      srcDir,
      extension.options.resources.client.component
    ),
  }));
}

function filterSiteComponents(
  extension: BaseExtension
): extension is SiteComponent {
  return extension.type === 'SiteComponent';
}
