import { ok } from 'node:assert';
import {
  dirname,
  isAbsolute as isAbsolutePath,
  join,
  relative,
} from 'node:path';
import { fileURLToPath } from 'node:url';
import type { WixIntegration } from '@wix/astro-core';
import type { DevCenterEmbeddedScript } from '@wix/dev-center-schemas';
import type { Rollup, ViteDevServer } from 'vite';
import { viteBuildOutputToRollupOutputs } from '@wix/vite-utils';
import { parseFragment, serialize } from 'parse5';
import { build, createServer } from 'vite';
import type { EmbeddedScript } from './builders.js';
import { filterSupportedExtensions } from './utils/filterSupportedExtensions.js';
import { getAssetContent } from './utils/getAssetContent.js';
import { getViteConfig } from './utils/getViteConfig.js';
import { traverseModuleScripts } from './utils/htmlUtils.js';
import { isAbsoluteUrl } from './utils/isAbsoluteUrl.js';

export const createIntegration = (): WixIntegration => {
  let rootDir: null | string = null;
  let srcDir: null | string = null;
  let extensions: EmbeddedScript[] | null = null;

  let customBuildOutput: null | Rollup.RollupOutput = null;
  let customDevServer: null | ViteDevServer = null;

  return {
    name: '@wix/astro/embedded-scripts',
    hooks: {
      'astro:config:setup'({ config }) {
        rootDir = fileURLToPath(config.root);
        srcDir = fileURLToPath(config.srcDir);
      },
      'wix:model:setup'({ model }) {
        extensions = model.extensions.filter(filterSupportedExtensions);
      },
      'wix:model:changed'({ model }) {
        extensions = model.extensions.filter(filterSupportedExtensions);
      },
      async 'wix:server:start'({
        createExtensionViteConfig,
        setupExtensionProxy,
      }) {
        ok(extensions);
        ok(srcDir != null);

        const config = getViteConfig({
          createExtensionViteConfig,
          extensions,
          srcDir,
        });

        customDevServer = await createServer(config);

        await customDevServer.listen();

        setupExtensionProxy(customDevServer);
      },
      async 'astro:server:done'() {
        ok(customDevServer);
        await customDevServer.close();
      },
      async 'wix:build:setup'({ createExtensionViteConfig }) {
        ok(extensions);

        if (extensions.length === 0) {
          return;
        }

        ok(srcDir != null);

        const buildOutput = await build(
          getViteConfig({
            createExtensionViteConfig,
            extensions,
            srcDir,
          })
        );

        const [clientOutput] = viteBuildOutputToRollupOutputs(buildOutput);
        ok(clientOutput);

        customBuildOutput = clientOutput;
      },
      async 'wix:app-manifest:setup'({
        staticsUrlPlaceholder,
        updateAppManifest,
      }) {
        ok(extensions);
        ok(srcDir != null);
        ok(rootDir != null);

        const components: DevCenterEmbeddedScript[] = [];

        for (const extension of extensions) {
          const fileName = relative(
            rootDir,
            join(srcDir, extension.options.source)
          );

          const html = await getAssetContent({
            buildOutput: customBuildOutput,
            fileName,
            viteDevServer: customDevServer,
          });

          const fragment = parseFragment(html);

          traverseModuleScripts(fragment, (script) => {
            const srcAttr = script.attrs.find(
              (x) => x.name === 'src' || x.name === 'href'
            );
            ok(srcAttr);

            if (
              srcAttr.value.startsWith('//') ||
              isAbsoluteUrl(srcAttr.value)
            ) {
              return;
            }

            const normalizedValue = isAbsolutePath(srcAttr.value)
              ? srcAttr.value
              : join(
                  customDevServer?.config.base ?? '',
                  dirname(fileName),
                  srcAttr.value
                );

            const newValue = new URL(normalizedValue, staticsUrlPlaceholder);
            srcAttr.value = newValue.toString();
          });

          components.push({
            compId: extension.options.id,
            compType: 'EMBEDDED_SCRIPT',
            compData: {
              embeddedScriptComponentData: {
                name: extension.options.name,
                allowedForFreeSite: true,
                connectArticleUrl: '',
                dependencies: [],
                embedCategory: extension.options.scriptType,
                loadOnce: true,
                pages: 'NONE_PAGES',
                placement: extension.options.placement,
                template: serialize(fragment),
              },
            },
          });
        }

        updateAppManifest({
          components,
        });
      },
    },
  };
};
